<template>
  <div>
    <h1>Мои доски</h1>
    <div class="boards">
      <button class="addBoard" v-on:click="showModal">+ Создать доску</button>
      <HomeBoard
        v-bind:board="board"
        v-for="(board, index) in BOARDS"
        :key="board.id"
        v-bind:index="index"
      />
      <ModalWindow v-if="isModalVisible" v-on:remove-modal="removeModal" />
    </div>
    <!-- <router-link to="/board"></router-link> -->
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import ModalWindow from '@/components/ModalWindow.vue';
import HomeBoard from './HomeBoard.vue';

export default {
  components: {
    HomeBoard,
    ModalWindow,
  },
  computed: {
    ...mapGetters(['BOARDS']),
  },
  methods: {
    ...mapActions(['GET_BOARDS_FROM_API']),
    showModal() {
      this.isModalVisible = true;
    },
    removeModal() {
      this.isModalVisible = false;
    },
  },
  async mounted() {
    await this.GET_BOARDS_FROM_API();
  },
  data() {
    return {
      isModalVisible: false,
    };
  },
};
</script>
<style scoped>
.boards {
  display: grid;
  grid-template-columns: repeat(4, 0.4fr);
  grid-gap: 20px;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
}
h1 {
  color: #fff;
}
.addBoard {
  height: 100px;
  background-color: hsla(0, 0%, 100%, 0.3);
  text-align: center;
  border: none;
  cursor: pointer;
}
.addBoard:hover {
  background-color: hsla(0, 0%, 100%, 0.5);
}
</style>
