<template>
  <div class="main" ref="modal_wrapper">
    <div class="modal_wrapper">
      <div class="modal">
        <!-- <form v-on:submit.prevent="createBoard">
          <label for="boardName"><h3>Введите название доски</h3></label>
          <input
            type="text"
            id="boardName"
            placeholder="Пишите название здесь"
            v-model="title"
          />
          <button type="submit" v-on:click="removeModal">Создать</button>
        </form> -->
        <div class="div_form">
          <label for="boardName"><h3>Введите название доски</h3></label>
          <input
            type="text"
            id="boardName"
            placeholder="Пишите название здесь"
            v-model="title"
          />
          <button type="button" v-on:click="removeModal(), createBoard()">
            Создать
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions } from 'vuex';

export default {
  name: 'ModalWindow',
  data() {
    return {
      title: '',
    };
  },
  methods: {
    ...mapActions(['CREATE_BOARD', 'GET_BOARDS_FROM_API']),
    removeModal() {
      this.$emit('remove-modal');
    },
    createBoard() {
      this.CREATE_BOARD({ title: this.title });
      this.GET_BOARDS_FROM_API();
    },
  },
  mounted() {
    const vm = this;
    document.addEventListener('click', (item) => {
      if (item.target === vm.$refs.modal_wrapper) {
        vm.removeModal();
      }
    });
    const input = document.querySelector('input');
    input.focus();
  },
};
</script>
